@import "https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap";
* {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
  font-family: Ubuntu, sans-serif;
}

ul, ol, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  --header-height: 150px;
  --header-button-width: 100px;
  --background-color: #fafafa;
  --color: #010101;
  min-height: 100vh;
  background-color: var(--background-color);
  color: var(--color);
  overflow-x: hidden;
}

body.light {
  --background-color: #fafafa;
  --color: #262626;
}

body.dark {
  --background-color: #262626;
  --color: #fafafa;
}

header {
  z-index: 2;
  width: 200vw;
  height: var(--header-height);
  background-color: #9696964d;
  display: flex;
  position: absolute;
  top: 0;
  left: -100vw;
  box-shadow: 0 0 30px #00000080;
}

header.transition {
  transition: left .5s ease-in-out;
}

header.opened {
  left: 0;
}

header .header-button {
  width: var(--header-button-width);
  flex-basis: var(--header-button-width);
  height: var(--header-height);
  justify-content: center;
  align-items: center;
  display: flex;
}

header .header-button svg {
  width: 25px;
  height: 25px;
  fill: var(--color);
}

header #nav-side {
  width: 100vw;
  height: var(--header-height);
  display: flex;
}

header #nav-side nav, header #nav-side nav ul {
  flex-grow: 1;
  display: flex;
}

header #nav-side nav ul li {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
}

header #nav-side nav ul li:nth-child(2n+1) {
  background-color: #96969600;
}

header #nav-side nav ul li:nth-child(2n+1):hover {
  background-color: #9696964d;
}

header #main-side {
  width: 100vw;
  height: var(--header-height);
  justify-content: space-between;
  display: flex;
}

header #main-side #photo-name {
  justify-content: center;
  align-items: center;
  display: flex;
}

header #main-side #photo-name img {
  object-fit: cover;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin-right: 10px;
  scale: .8;
}

header #main-side #photo-name h1 {
  font-size: 40px;
}

header #main-side #photo-name p {
  font-size: 20px;
}

#main {
  top: var(--header-height);
  min-height: calc(100% - var(--header-height));
  flex-direction: column;
  display: flex;
  position: absolute;
  left: 0;
}

#main main {
  flex-grow: 1;
  padding: 40px;
}

#main main h2 {
  padding: 15px;
  font-size: 25px;
}

#main main p {
  padding: 10px 0;
  font-size: 18px;
}

.button {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #9696961a;
  transition: background-color .1s ease-in-out;
}

.button:hover {
  background-color: #9696964d;
}

footer {
  height: var(--header-height);
  z-index: 2;
  width: 100vw;
  background-color: #9696964d;
  box-shadow: 0 0 30px #00000080;
}

